	module.exports = {"data":{"site":{"siteMetadata":{"title":"yerevancoder","author":"Coders of Armenia"}},"markdownRemark":{"id":"/Users/holykill/Documents/iterate/yerevancoder.github.io/src/pages/2018-03-14-Front-End-ReactJS-Developer-Learning-Path/index.md absPath of file >>> MarkdownRemark","html":"<h2>General Requirements</h2>\n<p>Note: This list is proposed by RenderForest, for newbie devs that apply for a job in our company.\nThis is not a complete list, nor it is a single answer to all questions.\nBut we thought that this might be helpful for community and for those who want to learn modern front-end development with ReactJs technology.</p>\n<ul>\n<li>Basic understanding of HTML/CSS <a href=\"https://www.codecademy.com/en/tracks/web\">(learn)</a></li>\n<li>Have already read this <a href=\"https://developer.mozilla.org/en-US/docs/Web/JavaScript/A_re-introduction_to_JavaScript\">(A reintroduction to JavaScript)</a></li>\n<li>Basic working knowledge of JavaScript</li>\n<li>CS Related university grad or 3rd course or above.</li>\n</ul>\n<h2>General JavaScript Knowledge</h2>\n<ul>\n<li>\n<p>Git / github - or Bitbucket <a href=\"https://try.github.io/levels/1/challenges/1\">(learn)</a></p>\n</li>\n<li>\n<p>Basic understanding of</p>\n<ul>\n<li>NodeJs npm (npm install, uninstall, —save …)</li>\n<li>Webpack <a href=\"https://webpack.js.org/guides/getting-started/\">(learn)</a>\n<a href=\"https://auth0.com/blog/webpack-a-gentle-introduction/\">(learn)</a></li>\n<li>Asynchronous functions <a href=\"https://medium.com/@siddharthac6/javascript-execution-of-synchronous-and-asynchronous-codes-40f3a199e687\">(learn)</a></li>\n<li>Promises\n<a href=\"https://medium.com/javascript-scene/master-the-javascript-interview-what-is-a-promise-27fc71e77261\">(learn)</a>\n<a href=\"https://learn.javascript.ru/promise\">(learn)</a></li>\n</ul>\n</li>\n<li>\n<p>Be familiar with</p>\n<ul>\n<li>ES6 syntax <a href=\"https://webapplog.com/es6/\">(learn)</a></li>\n<li>What are Cookies? <a href=\"https://www.nczonline.net/blog/2009/05/05/http-cookies-explained/\">(learn)</a>\n<a href=\"https://www.w3schools.com/js/js_cookies.asp\">(learn)</a></li>\n<li>What is package.json for? <a href=\"http://nodesource.com/blog/the-basics-of-package-json-in-node-js-and-npm/\">(learn)</a></li>\n<li>Have written an array sorting function (bubble sort, quick sort.. Or any)\n<a href=\"http://www.8bitavenue.com/2015/09/sorting-algorithms-explained-by-examples/\">(learn)</a>\n<a href=\"https://www.cs.cmu.edu/~adamchik/15-121/lectures/Sorting%20Algorithms/sorting.html\">(learn)</a>\n<a href=\"http://macr.ae/article/sorting-algorithms.html\">(learn)</a></li>\n</ul>\n</li>\n<li>\n<p>General Help - <a href=\"https://nodeschool.io/#workshoppers\">(learn)</a></p>\n</li>\n</ul>\n<h2>For Front-End (ReactJS) development.</h2>\n<ul>\n<li>\n<p>Do any small project on ReactJS, understand the component lifecycle.</p>\n<ul>\n<li><a href=\"https://reactjs.org/docs/state-and-lifecycle.html\">(state)</a>,\n<a href=\"https://reactjs.org/docs/react-component.html\">(component)</a></li>\n</ul>\n</li>\n<li>Get familiar with redux <a href=\"https://egghead.io/courses/getting-started-with-redux\">(learn)</a></li>\n<li>\n<p>FlowType - read a bit about static type checking in JavaScript and watch these videos</p>\n<ul>\n<li><a href=\"https://www.youtube.com/watch?v=TjmTuglpAXQ\">(Video 1)</a>,\n<a href=\"https://www.youtube.com/watch?v=oaBzhS6O4ew\">(Video 2)</a>,\n<a href=\"https://www.youtube.com/watch?v=V1po0BT7kac\">(Video 3)</a></li>\n</ul>\n</li>\n</ul>\n<h2>Additional Topics To Study</h2>\n<ul>\n<li>Mastering JavaScript Callbacks <a href=\"https://github.com/emarukyan/mastering-callbacks-javascript/\">(tasks)</a></li>\n<li>Recursion <a href=\"https://github.com/AlbertHambardzumyan/recursion\">(learn)</a></li>\n<li><a href=\"https://teachyourselfcs.com/\">Teach Yourself Computer Science</a></li>\n</ul>","frontmatter":{"title":"Front End (ReactJS) Developer Learning Path","tags":"front-end, JavaScript, ReactJS","author":"Renderforest","date":"March 14, 2018","discussionId":"2018-03-14-Front-End-ReactJS-Developer-Learning-Path"}}},"pathContext":{"slug":"/2018-03-14-Front-End-ReactJS-Developer-Learning-Path/"}}

